* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --font-color: #1a1a1a;
  --background-color: #f7f7f7;
  --keyboard-color: #e6e6e6;
  --letter-box-color: #fff;
  --ball-color: #090909;
  --ball-background: #e6e6e6;
  --transition--time: .6s;
  --gray-overlay: #666;
}

.active--dark {
  --font-color: #e6e6e6;
  --background-color: #1a1a1a;
  --keyboard-color: #666;
  --letter-box-color: #222;
  --ball-color: #f7f7f7;
  --ball-background: #5e5e5e;
  --modal-font-color: #ccc;
  --gray-overlay: #3c3c3c;
}

body {
  background-color: var(--background-color);
  transition: all var(--transition--time) linear 0s;
}

.disable-select {
  -webkit-user-select: none;
  user-select: none;
}

header {
  border-bottom: 2px solid var(--ball-background);
  justify-content: space-between;
  transition: inherit;
  display: flex;
}

header p {
  color: var(--font-color);
  border-right: none;
  justify-content: center;
  margin: 0 10px;
  font-family: Bebas Neue, sans-serif;
  font-size: 3.2rem;
  transition: inherit;
  right: 100px;
}

.btn--container {
  justify-content: center;
  align-items: center;
  margin: 0 16px;
  padding: 4px 0;
  transition: inherit;
  display: flex;
}

.btn {
  background: none;
  border: none;
  margin: 0 12px;
  font-size: 1.5rem;
}

.btn:hover, #toggle--theme {
  cursor: pointer;
}

#toggle--theme {
  height: 30px;
  background-color: var(--ball-background);
  transition: all var(--transition-time) ease 0s;
  border: 2px solid #000;
  border-radius: 50px;
  justify-content: space-between;
  align-items: center;
  margin: 0 20px;
  font-size: 1rem;
  display: flex;
  position: relative;
}

.icon {
  margin: 0 6px;
  font-size: 1rem;
}

.fa-circle-question, .fa-arrow-rotate-left {
  color: var(--font-color);
  transition: inherit;
}

.ball {
  height: 22px;
  width: 22px;
  background-color: var(--ball-color);
  transition: all var(--transition--time) ease 0s;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 2px;
}

.toggle--dark {
  transform: translateX(29px);
}

.fa-sun {
  color: orange;
}

.fa-moon {
  color: #ff0;
}

.hidden {
  display: none;
}

.modal {
  min-height: 500px;
  min-width: 500px;
  max-height: 750px;
  max-width: 750px;
  width: 70%;
  background-color: var(--background-color);
  z-index: 10;
  color: var(--modal-font-color);
  border-radius: 5px;
  padding: 50px;
  transition: inherit;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3rem 5rem #0000004d;
}

.modal h1 {
  color: var(--modal-font-color);
  padding: 0 0 36px;
  font-family: Vazirmatn, sans-serif;
}

.close-modal {
  color: var(--font-color);
  cursor: pointer;
  background: none;
  border: none;
  font-size: 3rem;
  position: absolute;
  top: 1.2rem;
  right: 2rem;
}

.overlay {
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  z-index: 5;
  background-color: #0009;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  font-family: Roboto, sans-serif;
}

.modal p {
  color: var(--modal-font-color);
  padding: 0 4px 16px;
}

.example--box {
  height: 40px;
  width: 40px;
  background-color: var(--letter-box-color);
  transition: background-color var(--transition-time) linear 0s;
  border: .5px solid #9d9d9d;
  justify-content: center;
  align-items: center;
  margin: 0 1px;
  font-family: Nunito, sans-serif;
  font-size: 1.4rem;
  display: flex;
  box-shadow: inset 0 0 1px 1px #00000026;
}

.message--container, .guess--container {
  height: 32px;
  transition: all var(--transition--time) linear 0s;
  justify-content: center;
  align-items: center;
  margin: 0 0 12px;
  font-size: 1.2rem;
  display: flex;
}

.message--container p, .guess--container p {
  background-color: var(--ball-background);
  color: var(--font-color);
  border-radius: 10px;
  padding: 4px 8px;
  font-family: Roboto, sans-serif;
  transition: inherit;
}

.game {
  justify-content: center;
  margin: 20px 0 0;
  display: flex;
}

.game--row {
  margin: 4px;
  display: flex;
}

.letter--box {
  height: 60px;
  width: 60px;
  color: var(--font-color);
  background-color: var(--letter-box-color);
  transition: all var(--transition--time) linear 0s;
  border: 2px solid #9d9d9d;
  justify-content: center;
  align-items: center;
  margin: 0 2px;
  font-family: Nunito, sans-serif;
  font-size: 2rem;
  display: flex;
  box-shadow: inset 0 0 1px 1px #00000026;
}

.green--overlay {
  transition: background-color 0s linear .25s;
  background-color: #4f9e48 !important;
}

.yellow--overlay {
  transition: background-color 0s linear .25s;
  background-color: #c9b12a !important;
}

.gray--overlay {
  transition: background-color 0s linear .25s;
  background-color: var(--gray-overlay) !important;
}

.tile--flip {
  animation: flip .5s linear;
}

@keyframes flip {
  0% {
    transform: rotateX(0deg);
  }

  50% {
    transform: rotateX(90deg);
  }

  100% {
    transform: rotateX(0deg);
  }
}

.keyboard {
  justify-content: center;
  margin: 32px 0 0;
  display: flex;
}

.keyboard--row {
  justify-content: center;
  display: flex;
}

.keyboard--key {
  width: 40px;
  height: 40px;
  background-color: var(--keyboard-color);
  color: var(--font-color);
  transition: all var(--transition--time) ease 0s;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin: 4px;
  font-family: Roboto, sans-serif;
  font-size: 1.1rem;
  display: flex;
  box-shadow: 0 2px 4px #0006, 0 7px 13px -3px #0000004d, inset 0 -3px #0003;
}

#enter-key {
  width: 70px;
}

